import React from "react";

const Results = (props) => {

    const { repos } = props;
    console.log("Repos is: ", repos);
    console.log(repos.length)

    const listRepos = 
        repos.length !== 0 ? (
            
        repos.map((item) => <li>{item.name}, {item.stargazers_count} stars</li>)
    ) : (
        <a></a>
    )

    return (
        <ul>{listRepos}</ul>
    );
}

export default Results;