import { useState, useEffect } from 'react';
import { Form, Card, Icon, Image, Button } from 'semantic-ui-react'
import logo from './logo.svg';
import Results from './components/Results'
import './App.css';
import React from 'react';
import axios from 'axios';


function App() {

  const [name, setName] = useState('');
  const [username, setUserame] = useState('');
  const [followers, setFollowers] = useState('');
  const [repos, setRepos] = useState('');
  const [avatar, setAvatar] = useState('');
  const [userInput, setUserInput] = useState('');
  const [error, setError] = useState(null)
  const [reposList, setReposList] = useState([])
  const [reposError, setReposError] = useState(null)

  useEffect(() => {
    fetch("https://api.github.com/users/example")
      .then(res => res.json())
      .then(data => {
        setData(data)
      });
  }, []);

  const setData = ({
    name, 
    login, 
    followers, 
    public_repos, 
    avatar_url
  }) => {
    setName(name);
    setUserame(login);
    setFollowers(followers);
    setRepos(public_repos);
    setAvatar(avatar_url);
  }

  const handleSearch = (e) => {
    setUserInput(e.target.value)
  }

  const handleSubmit = async () => {
    try {
      const result = await axios(`https://api.github.com/users/${userInput}`)
      console.log(result.data)
      setData(result.data)
      setReposList([])
      setError(null)
    } catch (e) {
      setError("Not fonud")
    }
  };

  const handleRepos = async () => {
    try {
      const result = await axios(`https://api.github.com/users/${userInput}/repos?limit=5`)
      setReposList(result.data)

      setReposError(null)
    } catch (e) {
      setReposError("Not found")
    }
  };

  return (
    <div>
      <div className='navbar'>Github Search</div>
      <div className='search'>
        <Form onSubmit={handleSubmit}>
            <Form.Group>
              <Form.Input
                placeholder='Name'
                name='name'
                onChange={handleSearch}
              />
              <Form.Button content='Submit' />
            </Form.Group>
          </Form>
      </div>
      { error ? (
        <div style={{display: 'flex',  justifyContent:'center', alignItems:'center'}}>
          <h1> {error} </h1>
        </div>
      ) : (
        <div>

          <div className='card'>
          <Card>
            <Image src={avatar} wrapped ui={false} />
            <Card.Content>
              <Card.Header>{name}</Card.Header>
              <Card.Header>{username}</Card.Header>
            </Card.Content>
            <Card.Content extra>
              <a>
                <Icon name='followers' />
                {followers} Followers
              </a>
            </Card.Content>
            <Card.Content extra>
              <a onClick={handleRepos}>
                <Icon name='repos' />
                {repos} Repos
              </a>
                <Results repos={reposList}></Results>
            </Card.Content>
          </Card>
        </div>
       </div>
      )}
      
    </div>
  );
}

export default App;
